import Vue from 'vue';
import VueRouter from 'vue-router';
import { taxiRouteNames, carsharingRouteNames } from './helpers';
import AuthPage from '../views/auth-page/auth-page.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'home',
    component: AuthPage,
  },
  {
    path: '/carsharing',
    name: carsharingRouteNames.overview,
    component: () => import('../views/carsharing-page/carsharing-page.vue'),
  },
  {
    path: '/catalog',
    name: carsharingRouteNames.catalog,
    component: () => import('../views/cars-catalog-page/cars-catalog-page.vue')
  },
  {
    path: '/car',
    name: carsharingRouteNames.car,
    component: () => import('../views/car-page/car-page.vue')
  },
  {
    path: '/user',
    name: carsharingRouteNames.user,
    component: () => import('../views/user-page/user-page.vue')
  },
  {
    path: '/taxi',
    name: taxiRouteNames.overview,
    component: () => import('../views/taxi-page/taxi-page.vue'),
  },

  {
    path: '/drivers',
    name: taxiRouteNames.drivers,
    component: () => import('../views/drivers-page/drivers-page.vue'),
  },
  {
    path: '/driver',
    name: taxiRouteNames.driver,
    component: () => import('../views/driver-page/driver-page.vue'),
  },
];

const router = new VueRouter({
  mode: 'hash',
  routes,
});

export default router;
