function convertCamelToKebabCase(string) {
  return string.replace(/([A-Z])/g, (bigChar) => `-${bigChar[0].toLowerCase()}`);
}

export default (
  blockName,
  properties,
  ignore = [],
  $stylesModule = {},
) => {
  const result = {};
  const isModule = Object.keys($stylesModule).length > 0;

  Object.keys(properties)
    .filter((key) => !ignore.includes(key))
    .forEach((key) => {
      const propertyValue = properties[key];
      const modifierValue = typeof propertyValue === 'boolean' ? '' : `_${propertyValue}`;
      const selector = convertCamelToKebabCase(`${blockName}_${key}${modifierValue}`);
      const modifierName = isModule && $stylesModule[selector] ? $stylesModule[selector] : selector;
      result[modifierName] = !!propertyValue;
    });

  return result;
};
