<template>
  <component
    :is="tag"
    :href="href"
    class="ui-button"
    :class="classesByProps"
    role="button"
    @click="emitClick"
  >
    <slot>
      {{ title }}
    </slot>
  </component>
</template>

<script>
import classesByProperties from '@/helpers/classes-by-properties';

export default {
  name: 'UiButton',

  props: {
    tag: {
      type: String,
      default: 'button',
      validator: (value) => ['button', 'a', 'div'].includes(value),
    },

    disabled: {
      type: Boolean,
      default: false,
    },

    pending: {
      type: Boolean,
      default: false,
    },

    fullwidth: {
      type: Boolean,
      default: false,
    },

    color: {
      type: String,
      default: '',
    },

    href: {
      type: String,
      default: undefined,
    },
  },

  computed: {
    classesByProps() {
      return classesByProperties('ui-button', this.$props, ['title', 'href']);
    },
  },

  methods: {
    emitClick(event) {
      if (this.disabled) return;
      this.$emit('click', event);
    },
  },
};
</script>

<style>
.ui-button {
  position: relative;
  box-sizing: border-box;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  color: #fff;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  user-select: none;
  background-color: #007dbc;
  box-shadow: 0px 0px 15px rgba(62, 73, 88, 0.1);
  border-radius: 15px;
  outline: none;
  border: none;
  font-family: 'SF Pro Text';
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  padding: 15px 10px;
  transition: all 0.15s;
}

.ui-button:active {
  transform: scale(0.95);
}

.ui-button:hover {
  opacity: 0.85;
}

.ui-button_color_white {
  background: #fff;
  color: #3e4958;
}

.ui-button_color_black {
  background-color: #3e4958;
  color: #fff;
}

.ui-button_color_transparent {
  background: transparent;
  color: #3e4958;
  border-radius: 0;
  box-shadow: none;
  padding: 0;
}

.ui-button_color_white-red {
  background: #fff;
  color: #f52d56;
}

.ui-button_fullwidth {
  display: block;
  width: 100%;
}
</style>
